@import "../../global.scss";

.navbar {
  width: 100%;
  height: 70px;
  background-color: $mainColor;
  color: white;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      display: flex;
      align-items: center;
      .logo {
        font-family: "Poppins";
        font-size: 33px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 30px;
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-right: 30px;

        @include mobile {
          display: none;
        }

        .icon {
          font-size: 18px;
          margin-right: 5px;
        }

        span {
          font-size: 15px;
          font-weight: 600;

          a {
            color: white;
            text-decoration: none;

            &:visited {
              color: white;
            }

            &:hover {
              text-decoration: underline;
              cursor: pointer;
            }

            &:active {
              color: white;
              text-decoration: underline;
            }
          }
        }
      }
    }

    .right {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;

        span {
          width: 100%;
          height: 3px;
          background-color: white;
          transform-origin: left;
          transition: all 1s ease;
        }
      }
    }
  }

  &.active {
    background-color: $mainColor;
    color: white;

    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
